import React, { useState, useEffect } from 'react';
import Logo from '../logo/Logo';
import { Link, useHistory } from "react-router-dom";
import BalloonButton from '../balloon-button/BalloonButton';

import '../../index.css';
import { useAuth } from '../../contexts/AuthContext/AuthContext';
import Avatar from '../avatar/Avatar';
import Popover from '../popover/Popover';

function NavBar({ classNames }) {
  const [menuVisibility, setMenuVisibility] = useState(false);
  const { state: { names, id, gender, profile_picture }, testToken, logout} = useAuth();
  const history = useHistory();

  const handleMenuVisibility = () => {
    setMenuVisibility(prev => !prev);
  };

  useEffect(async () => {
    //console.log(token);
    async function checkToken() {
      const localToken = localStorage.getItem('voku_token');
      if (localToken) {
        await testToken(localToken);
      }
    }

    await checkToken();
  }, [])

  return (
    <div className={ `flex justify-between py-3 ${classNames || ''}` }>
      <div className="flex items-center">
        <Logo textColor="white" linkTo="/" />

        <div className="hidden space-x-10 md:block roboto-font">
          <Link
            to="/call" 
            className="hidden mt-auto text-2xl text-gray-light hover:text-secondary">
            About
          </Link>

          <a
            href="https://github.com/voku-org/voku-site"
            className="mt-auto text-2xl text-gray-light hover:text-secondary"
            target="_blank" rel="noopener noreferrer"
          >
            Donate
          </a>
          
          <a
            href="https://github.com/voku-org/voku-site"
            className="mt-auto text-2xl text-gray-light hover:text-secondary"
            target="_blank" rel="noopener noreferrer"
          >
            GitHub
          </a>
        </div>
      </div>

      <div className='items-center justify-self-end'>
        { id
        ? <Popover
          id={'user_popover'}
          options={[
            {
              text: 'Profile details',
              onClick: () => history.push('/user/'+id)
            },
            {
              text: 'Log out',
              onClick: () => logout(() => history.replace('/'))
            }
          ]}
          className={'p-2 flex flex-row justify-center items-center'}>
            <Avatar gender={gender} src={profile_picture} classNames='w-fit h-fit' sizes={'w-16 h-16'}/>
            <h3 className='ml-3'>{names}</h3>
          </Popover>
        : <div className="space-x-5">
            <Link to={"/login"}>
              <BalloonButton
                side="left"
                text="Sign In"
                classNames="bg-trasparent text-white font-semibold roboto-font text-sm border-4 border-gray hover:border-gray-light h-10 py-1"
              />
            </Link>
            <Link to={"/signup"}>
              <BalloonButton
                side="right"
                text="Sign Up"
                classNames="bg-secondary hover:bg-secondary-dark hover:border-secondary-dark text-white font-semibold roboto-font text-sm border-secondary h-10 py-1"
              />
            </Link>
          </div>

        }
      </div>

      { /* Open mobile menu button */ }
      <button
        onClick={ handleMenuVisibility }
        className="block md:hidden focus:outline-none"
      >
        <svg
          className="text-white w-7 h-7"
          xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor"
        >
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
        </svg>
      </button>

      { /* Mobile Menu */ }
      <div
        className={
          "fixed top-0 flex flex-col justify-center left-0 z-10 w-screen h-full pt-4 bg-dark-secondary" +
          (menuVisibility ? " block" : " hidden")
        }
      >
        { /* Header with close button */ }
        <div className="flex justify-end pr-3 mb-10">
          <button
            onClick={ handleMenuVisibility }
            className="focus:outline-none"
          >
            <svg
              className="text-white w-7 h-7" 
              xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        { /* Menu items */ }
        <div className="flex flex-col h-full roboto-font">
          <div className="flex flex-col">

            <Link to="/about" 
              onClick={ handleMenuVisibility }
              className="hidden mb-6 text-3xl text-gray-ligh" href="#">
              About
            </Link>

            <a
              onClick={ handleMenuVisibility }
              href="https://github.com/videovoko/videovoko-org/"
              className="mb-6 text-3xl text-gray-light"
              target="_blank" rel="noopener noreferrer"
            >
              Donate
            </a>
            
            <a
              onClick={ handleMenuVisibility }
              href="https://github.com/voku-org/voku-site"
              className="text-3xl text-gray-light"
              target="_blank" rel="noopener noreferrer"
            >
              GitHub
            </a>

          </div>

          <div className="flex items-end justify-center h-full pb-2">
            <p className="text-lg text-left text-gray-400">
               2022 voku.org
            </p>
          </div>
        </div>
      </div>

    </div>
  );
}

export default NavBar;