import React from 'react';

const SelectPicker = ({className, name, value, onChange, options}) => {

  return (
    <select
      className={`w-full h-10 px-4 py-1 overflow-auto border-2 border-solid rounded-lg resize-none border-gray bg-dark-secondary focus:outline-none ${className}`}
      name = { name }
      defaultValue = {value}
      onChange = { onChange } 
    >
      <option value={""}>Select...</option>
      {options && options.map( item => {
        return <option value={item.value}>{item.text}</option>
      })}
    </select>
  )

}

export default SelectPicker;