import React, { useState, useEffect } from "react";
import {ReactComponent as AVF} from '../../assets/avatar-f.svg'
import {ReactComponent as AVM} from '../../assets/avatar-m.svg'

const Avatar = ({src, classNames, sizes, gender, onClick, ...props}) => {
  
  const [source, setSource] = useState('');

  useEffect(() => {

    function isBase64(str) {
      let base64regex = /^([0-9a-zA-Z+/]{4})*(([0-9a-zA-Z+/]{2}==)|([0-9a-zA-Z+/]{3}=))?$/;
      base64regex.test(str);
    }
    if(isBase64(src && src.split(",")[1]))
    {
      setSource(src);
    }
    else
    {
      setSource('');
    }
  }, []);

  return (
    <div onClick={onClick} className={`rounded-full cursor-pointer transition-all duration-200 ease-out bg-transparent border-2 border-gray ${classNames}`}>
      {src
      ? <image src={source} />
      : gender === 'Male'
        ? <AVM className={` ${sizes}`}/>
        :<AVF className={` ${sizes}`}/>
      }
    </div>
  );
}

export default Avatar;