import React from 'react';
import { ReactComponent as HomeIllustration } from '../../assets/home-illustration.svg';
import "../../index.css";
import { Link, useHistory } from "react-router-dom";
import { useForm } from "../../utils/useForm";
import Input from '../../components/input/Input';
import Card from '../../components/card/Card';
import LogoHeader from '../../components/logo-header/LogoHeader';
import { useAuth } from '../../contexts/AuthContext/AuthContext';

const initialForm = {
  email: "",
  password: "",
};

const validationsForms = (form) => {
  let errors = {};
  let regexEmail = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/;

  console.log(form);
  if (!regexEmail.test(form.email.trim())){
    errors.email = "Please enter a valid email";
  }
  return errors;
};

function Login({...props}) {

  const {
    form,
    errors,
    loading,
    response,
    handleChange,
    handleBlur,
    handleSubmit,
  } = useForm(initialForm, validationsForms);

  const {state: { token }, signin} = useAuth();
  const history = useHistory();

  // Login method
  async function loginRequest(data) {
    await signin(data.email, data.password, () => history.push("/"), (err) => console.log('hubo error', err));
  }

  return (
  <div className="w-full h-full px-3 md:h-screen md:px-24 bg-dark">
      
     <LogoHeader/>

      <div className="flex justify-center order-1 w-full h-full col-span-2 mb-8 md:col-span-1 md:order-1 md:mb-0">
          <HomeIllustration className="hidden object-none object-center w-3/5 my-6 h-4/5 md:w-10/12 md:block"/> 
          
        <Card classNames='my-20 px-10 w-10/12 h-auto md:w-3/4 md:h-2/4 bg-dark-tertiary rounded-lg'>
          <form onSubmit={(e) => handleSubmit(e, 0, loginRequest)} className='my-5'
            >
              <div className='text-white roboto-font borderButton'>
                
              <p className="my-2 text-left">Email</p>
                <Input
                  classNames="text-center w-full"
                  placeholder="Enter your email"
                  type="email"
                  name="email"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={form.email}
                />
                {errors.email && (
                  <p className="text-danger-light">{errors.email}</p>
                )}

                <p className="my-2 text-left">Password</p>
                <Input
                  classNames="text-center w-full"
                  placeholder="Enter your password"
                  type="password"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={form.password}
                />
                {errors.password && (
                  <p className="text-danger-light">{errors.password}</p>
                )}
              </div>
             
                <button className="w-full py-1 my-4 text-white rounded-lg bg-primary hover:bg-primary-dark border-primary hover:border-primary-dark ballon-right">
                  Ready
                </button>
              
              <div className='col-span-2 rezise-none'>
              <Link
                to="/Register" 
                className="tracking-wider text-gray-light hover:text-secondary">
                Don't have an account?
              </Link>
              </div>
          </form>
        </Card>
    </div>

  </div>

  );
  }

export default Login;