import React from 'react';

const Input = ({ type, name, value, classNames, placeholder, onChange, onBlur, onKeyDown, maxLength }) => {
  return(
    <input
      type={ type }
      name = { name }
      className={`${classNames || ''} h-10 px-4 py-1 overflow-auto border-2 border-solid rounded-lg resize-none border-gray bg-dark-secondary focus:outline-none`}
      defaultValue={ value }
      onChange={ onChange }
      onBlur={ onBlur }
      onKeyDown={ onKeyDown }
      placeholder={ placeholder }
      maxLength={ maxLength }
    ></input>
  );
};

export default Input;