import React, { useState } from "react";

import { ReactComponent as HomeIllustration } from "../../assets/welcome.svg";
import "../../index.css";
import { useForm } from "../../utils/useForm";
import Message from "../../components/message/Message";
import Input from "../../components/input/Input";

import LogoHeader from "../../components/logo-header/LogoHeader";
import Card from "../../components/card/Card";
import SelectPicker from "../../components/select-picker/SelectPicker";
import axios from "../../config/axios";
import { useHistory } from "react-router-dom";

const initialForm = {
  name: "",
  lastname: "",
  username: "",
  email: "",
  password: "",
  confirmPassword: "",
  gender: "",
  nationality: "",
  birthday: "",
};

const validationsForms = (form, page) => {
  let errors = {};
  let regexName = /^[A-Za-z\s]+$/;
  let regexUsername = /^.{5,12}$/;
  let regexEmail = /^(\w+[/./-]?){1,}@[a-z]+[/.]\w{2,}$/;
  let regexPassword = /^(?=\w*\d)(?=\w*[A-Z])(?=\w*[a-z])\S{8,16}$/;
  let regexDate = /^\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])$/

  if (!form.name.trim()) {
    errors.name = "";
  }else if (!regexName.test(form.name.trim())){
    errors.name = "Only letters and blank spaces are accepted";
  }

  if (!form.lastname.trim()) {
    errors.lastname = "";
  }else if (!regexName.test(form.lastname.trim())){
    errors.lastname = "Only letters and blank spaces are accepted";
  }

  if (!form.username.trim()) {
    errors.username = "";
  }else if (!regexUsername.test(form.username.trim())){
    errors.username = "The username must have between 5 and 12 characters";
  }

  if (!form.email.trim()) {
    errors.email = "";
  }else if (!regexEmail.test(form.email.trim())){
    errors.email = "Enter a email";
  }

  if (!form.password.trim()) {
    errors.password = "";
  }else if (!regexPassword.test(form.password.trim())){
    errors.password = "The password must have between 8 and 16 characters, at least one digit, at least one lower case and at least one upper case.";
  }

  if(form.password !== form.confirmPassword){
    errors.confirmPassword = "The passwords should match each other"
  }

  if(page === 2)
  {
    if(form.esperanto_level === ""){
      errors.esperanto_level = "This field is required"
    }
  
    if(!form.birthday.trim()) {
      errors.birthday = "";
    }else if (!regexDate.test(form.birthday.trim())){
      errors.birthday = "Enter a valid date in formta yyyy-mm-dd";
    }
  
    if(form.gender === ""){
      errors.gender = "This field is required"
    }
  
    if(form.nationality === ""){
      errors.nationality = "This field is required"
    }
  }


  return errors;
};

function Register() {
  const {
    form,
    errors,
    loading,
    response,
    handleChange,
    handleBlur,
    handleSubmit,
  } = useForm(initialForm, validationsForms);

  const [formPage, setFormPage] = useState(1);
  const history = useHistory();

  const handleFormPages = (type) => {
    console.log(type);
    let pageValue = formPage;
    if(type === 'next')
    {
      pageValue = pageValue + 1;
    }
    else
    {
      pageValue = pageValue - 1;
    }
    setFormPage(pageValue);
  }

  const signUp = async (data) => {
    const response = await axios.post('/auth/signup',{...data, registration_date: Date.now(), last_connection: Date.now()})
    console.log(response);
    if(response.data.email)
    {
      history.replace('/login');
    }
  }

  return (
    <div className="flex flex-col w-full h-full px-3 md:h-screen md:px-24 bg-dark">
      
      <LogoHeader/>

      <div className="grid content-center justify-center h-auto grid-cols-2 pt-3 my-10 md:my-20 md:pt-0">
        
        <div className="flex justify-center order-1 w-full h-full col-span-2 mb-8 md:col-span-1 md:order-2 md:mb-0">
          <HomeIllustration className="hidden object-none object-center w-full h-full md:w-11/12 md:block" />
        </div>

        <div className="flex flex-col order-2 col-span-2 md:col-span-1 md:order-1 md:px-5">

          <Card classNames="p-8 roboto-font bg-dark-tertiary rounded-lg">
            
              <h1 className="mb-3 text-2xl font-bold leading-snug text-center text-white md:text-center md:leading-snug md:text-2xl">
                Welcome!
                <font className="text-primary">.</font>
              </h1>

              <p className="mb-5 text-lg leading-normal text-center md:leading-normal md:text center md:text-2z text-gray-light">
                <span className="">
                  Welcome to the community. Please enter your data to set up
                  your profile.
                </span>
              </p>
            
            <form onSubmit={(e) => handleSubmit(e, formPage, signUp)}>
              { formPage === 1 &&
                <div className="text-white borderButton">
                  <p className="my-2 text-left text-white">Name</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your first name"
                    type="text"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.name}
                  />
                  {errors.name && (
                    <p className="text-danger">{errors.name}</p>
                  )}

                  <p className="my-2 text-left text-white">Last name</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your last name"
                    type="text"
                    name="lastname"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.lastname}
                  />
                  {errors.lastname && (
                    <p className="text-danger">{errors.lastname}</p>
                  )}

                  <p className="my-2 text-left text-white">User name</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Set your user name"
                    type="text"
                    name="username"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.username}
                  />
                  {errors.username && (
                    <p className="text-danger">{errors.username}</p>
                  )}

                  <p className="my-2 text-left text-white">Email</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your email"
                    type="email"
                    name="email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.email}
                  />
                  {errors.email && (
                    <p className="text-danger">{errors.email}</p>
                  )}

                  <p className="my-2 text-left text-white">Password</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your password"
                    type="password"
                    name="password"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.password}
                  />
                  {errors.password && (
                    <p className="text-danger">{errors.password}</p>
                  )}

                  <p className="my-2 text-left text-white">Confirm Password</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="Enter your password"
                    type="password"
                    name="confirmPassword"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.confirmPassword}
                  />
                  {errors.confirmPassword && (
                    <p className="text-danger">{errors.confirmPassword}</p>
                  )}
                </div>
              }
              { formPage === 2 &&
                <div className="text-white borderButton">
                  <p className="my-2 text-left text-white">Gender</p>
                  <SelectPicker
                    options={[{value: 'Male', text: 'Male'}, {value:'Female', text: 'Female'}]}
                    name='gender'
                    onChange={handleChange}
                    value={form.gender} 
                  />
                  {errors.gender && (
                    <p className="text-danger">{errors.gender}</p>
                  )}

                  <p className="my-2 text-left text-white">Esperanto Level</p>
                  <SelectPicker
                    options={[{value: 'Basic', text: 'Basic'}, {value:'Middel', text: 'Middle'}, {value:'Advanced', text: 'Advanced'} ]}
                    name='esperanto_level'
                    onChange={handleChange}
                    value={form.esperanto_level} 
                  />
                  {errors.esperanto_level && (
                    <p className="text-danger">{errors.esperanto_level}</p>
                  )}

                  <p className="my-2 text-left text-white">Nationality</p>
                  <SelectPicker
                    options={[{value: 'Mexican', text: 'Mexican'}, {value:'American', text: 'American'}]}
                    name='nationality'
                    onChange={handleChange}
                    value={form.nationality} 
                  />
                  {errors.nationality && (
                    <p className="text-danger">{errors.nationality}</p>
                  )}

                  <p className="my-2 text-left text-white">Birthday</p>
                  <Input
                    classNames="text-center w-full"
                    placeholder="YYYY-MM-DD"
                    type="text"
                    name="birthday"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={form.birthday}
                  />
                  {errors.birthday && (
                    <p className="text-danger">{errors.birthday}</p>
                  )}
                </div>
              }
              <div className="my-8">
                { formPage == 2
                ? <button
                    type="submit"
                    className="w-full py-1 text-white rounded-lg bg-primary hover:bg-primary-dark roboto-font border-primary hover:border-primary-dark ballon-right"
                  >
                    Ready
                  </button>
                : <button
                    onClick={(e) => handleSubmit(e, formPage, () => handleFormPages('next'))}
                    className="w-full py-1 text-white rounded-lg bg-primary hover:bg-primary-dark roboto-font border-primary hover:border-primary-dark ballon-right"
                  >
                    Next
                  </button>
                }
                { formPage > 1 &&
                  <button
                    onClick={(e) => handleSubmit(e, formPage, () => handleFormPages('prev'))}
                    className="w-full py-1 mt-2 text-white rounded-lg hover:bg-dark-tertiary bg-gray hover:border-dark-tertiary border-gray roboto-font ballon-right"
                  >
                    Back
                  </button>
                }
              </div>
              {response && (<Message msg="the data has been sent correctly" classNames="bg-primary"/> )}
            </form>
          </Card>
          
        </div>
      </div>

      <div className="hidden py-1 md:block">
        <p className="my-8 text-lg text-left text-gray-400 roboto-font">
           2022 voku.org
        </p>
      </div>
    </div>
  );
}

export default Register;
