import React, { createContext, useContext, useReducer, } from 'react';
import axios from '../../config/axios';

/**
 * Auth reducer
 */

const authReducer = (state, action) => {
  switch (action.type) {
    case 'signin':
      return {
        id: action.payload.id,
        token: action.payload.token,
        username: action.payload.username,
        email: action.payload.email,
        names: action.payload.names,
        profile_picture: action.payload.profile_picture,
        gender: action.payload.gender
      };
    case 'logout':
      return {
        id: '',
        token: '',
        username: '',
        email: '',
        names: '',
        profile_picture: '',
        gender: ''
      };
    default:
      return state;
  }
};

/* Actions */

/**
 * Sign in / log in
 */
const signin = dispatch => async ( email, password, onSuccess, onError ) => {
  try {
    const response = await axios.post('/auth/login', { username: email, password: password });
    console.log(response);
    dispatch({ type: 'signin', payload: {...response.data, token: response.data.access_token} });
    localStorage.setItem('voku_token', response.data.access_token);
    onSuccess();
  } catch (err) {
    console.error('ERROR: ' + err);
    onError(err);
  }
};


/**
 * Log out
 */
const logout = dispatch => onCompleted => {
  dispatch({ type: 'logout' });
  localStorage.removeItem('voku_token');
  // onCompleted is a callback function that redirects user to login
  onCompleted();
};

/**
 * Test saved token
 */
const testToken = dispatch => async token => {
  try {
    const response = await axios.get('/auth/verify', {
      headers: {
        Authorization: `Bearer ${token}`
      }
    });
    dispatch({ type: 'signin', payload: { ...response.data, token } });
  } catch (err) {
    localStorage.removeItem('voku_token');
  }
};

/* Context */
export const AuthContext = createContext();

/* Hook */
export function useAuth() {
  return useContext(AuthContext);
}

/* Provider */
export function AuthProvider({ children }) {

  const [state, dispatch] = useReducer(authReducer, {
    id: '',
    token: '',
    email: '',
    names: '',
    profile_picture: '',
    gender: '',
    username: ''
  });

  const boundActions = {
    signin: signin(dispatch),
    logout: logout(dispatch),
    testToken: testToken(dispatch)
  };

  return (
    <AuthContext.Provider value={{ state, ...boundActions }}>
      {children}
    </AuthContext.Provider>
  );
}